import React from "react";
import Link from "gatsby-link";

const NotFoundPage = () => (
  <div>
    <ul
      style={{
        margin: 100
      }}
    >
      <h1 style={{ color: "#D96969" }}>404</h1>
      <h2 style={{ alignSelf: "center" }}>No</h2>
      <h2 style={{ alignSelf: "center" }}>Page</h2>
      <h2 style={{ alignSelf: "center" }}>Found</h2>
      <Link
        to="/"
        style={{
          alignSelf: "center",
          margin: 50
        }}
      >
        Return Home
      </Link>
    </ul>
  </div>
);

export default NotFoundPage;



